<template>
  <div>
    <!-- <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <a @click="()=>{
          $router.go(-1)
        }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">设备列表</span>
    </mu-appbar>-->
    <van-nav-bar
      :fixed="true"
      title="设备列表"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="filter-o" slot="right" size="20" /> -->
    </van-nav-bar>

    <div class="body">
      <div v-for="(item,index) in itemList" :key="index">
        <div class="flexBox" @click="toDetails(item,index)">
          <div style="flex:3">
            <span>{{item.station}}</span>
          </div>
          <div class="flexBoxChild" style="flex:1">
            <div
              class="borderButton"
              :style="item.systemStatus===1
              ?normalColor
              :item.systemStatus===0
              ?warningColor
              :item.systemStatus==2
              ?MaintainColor
              :item.systemStatus===3
              ?warningColor:item.systemStatus===4
              ?warningColor
              :warningColor"
            >
              <span v-show="item.systemStatus===1">正常</span>
              <span v-show="item.systemStatus===0">异常</span>
              <span v-show="item.systemStatus===2">维护</span>
              <span v-show="item.systemStatus===3">离线</span>
              <span v-show="item.systemStatus===4">无水</span>
              <span v-show="item.systemStatus===8">停运</span>
            </div>
            <div class="imageSize">
              <img src="static/img/right.png" alt />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
export default {
  data() {
    return {
      itemList: [1, 1],
      normalColor: "background-color: #b9befc;color: #4d58e8;",
      warningColor: "background-color: #fcb9bf;color: #e84d54;",
      MaintainColor: "background-color: #f3e6ae;color: #e8cc4d;"
    };
  },
  methods: {
    toDetails(res, index) {
      this.$router.push({
        path: "DeviceDetails",
        query: {
          station: res.station,
          status: res.systemStatus
        }
      });
    },
    async initListData() {
      var url = "/station/monitor-system/datas/app";
      var search = null;
      var res = await AppBace.request(url, "GET", {}, this.token);
      console.log(res);
      this.itemList = res.data.data;
    }
  },
  created() {
    this.token = sessionStorage.getItem("token");
  },
  mounted() {
    this.initListData();
  }
};
</script>
<style  scoped>
.header {
  position: fixed;
  top: 0;
  width: 100%;
}
.body {
  padding: 0 10px;
  margin-top: 60px;
}
.imageSize img {
  width: 12px;
  height: 12px;
}
.flexBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  background-color: white;
  margin-top: 10px;
  padding: 5px;
}
.flexBoxChild {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.borderButton {
  /* border: 1px solid #ccc; */
  padding: 2px 10px;
  border-radius: 10px;
  background-color: #b9befc;
  color: #4d58e8;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.color {
  background-color: #f3e6ae;
}
</style>


